.progress {
    height: 17px;
    background: transparent;
    border: 1px solid #cceaff;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 1px 1px 0 rgba(0,0,0,.5), 0 1px 0 0 #fff;
            box-shadow: inset 0 1px 1px 0 rgba(0,0,0,.5), 0 1px 0 0 #fff;
    width: 80%;
    margin: 10px auto;
}
.progress > span {
    position: relative;
    float: left;
    margin: 0 -1px;
    height: 15px;
    line-height: 16px;
    text-align: right;
    background: #ccc;
    border: 1px solid;
    border-color: #bfbfbf #b3b3b3 #9e9e9e;
    border-radius: 10px;
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #e5ccff),color-stop(70%, #9687ff),to(#8865ff));
    background-image: linear-gradient(180deg,#e5ccff 0,#9687ff 70%,#8865ff);
    -webkit-box-shadow: inset 0 1px hsla(0,0%,100%,.3), 0 1px 2px rgba(0,0,0,.2);
            box-shadow: inset 0 1px hsla(0,0%,100%,.3), 0 1px 2px rgba(0,0,0,.2);
}
.progress > span:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    height: 15px;
    border-radius: 10px;
}
.progress > span > span {
    padding: 0 8px;
    font-size: 11px;
    font-weight: 700;
    color: #404040;
    color: rgba(255, 255, 255, 0.7);
    text-shadow: 0 1px hsla(0,0%,100%,.4);
}
