/*slideAlert*/
.hidden {
  display: none;
}
.overflowHide {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.sd-alert {
  text-align: left;
}
.sd-alert .sd-shadow {
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.6);
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  _position: fixed;
  pointer-events: auto;
}
.sd-alert .sd-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  border: none;
  background-color: #fff;
  overflow: hidden;
  z-index: 2000;
}
.sd-alert .sd-top-show {
  overflow-y: auto;
  height: 200px;
  -webkit-animation: sd-top-show 0.3s ease 1;
          animation: sd-top-show 0.3s ease 1;
}
.sd-alert .sd-top-hide {
  -webkit-animation: sd-top-hide 0.3s ease 1;
          animation: sd-top-hide 0.3s ease 1;
  height: 200px;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.sd-alert .sd-alert-show {
  width: 510px;
  height: 370px;
  background-color: rgba(0, 0, 0, 0);
  overflow-y: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: sd-alert-show 0.3s ease 1;
          animation: sd-alert-show 0.3s ease 1;
}
.sd-alert .sd-alert-hide {
  width: 510px;
  height: 370px;
  background-color: rgba(0, 0, 0, 0);
  overflow-y: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: sd-alert-hide 0.3s ease 1;
          animation: sd-alert-hide 0.3s ease 1;
}
@-webkit-keyframes sd-alert-show {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@keyframes sd-alert-show {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@-webkit-keyframes sd-alert-hide {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
@keyframes sd-alert-hide {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
